import React, { Component,useState } from 'react';
import '/node_modules/bootstrap/dist/css/bootstrap.min.css'
import 'bootstrap/dist/js/bootstrap.min'

import { Communatydata2 } from '../Data';
import '../About/About.css'
import ReactDOM from 'react-dom'
import ModalVideo from 'react-modal-video'
import { Autosliderbig } from '../slick-sld/Autosliderbig';



export function Communatysec2() {
   
    const communatydata2 = Communatydata2.map((communatydata2) => {
        return(
            <>
             <h4 className='blogh1'>{communatydata2.text}</h4>
             <h1 className='blogh2'>{communatydata2.text1}</h1>
            </>
            
        )
        })
    return( 
        <>
        <div className="communactin2un1">
            <div className="container">
                <div className="col-md-12 my-5">
                    <div className="communaction2unun1">
                    {communatydata2}
                    </div>
                </div>
            </div>
            <Autosliderbig />
        </div>
 
     
        </>
    )
}


