import React, { Component } from 'react';

import '../App.css';
import mainlgo from '../images/logo1.png'
import '/node_modules/bootstrap/dist/css/bootstrap.min.css'
import 'bootstrap/dist/js/bootstrap.min'
import "/node_modules/font-awesome/css/font-awesome.min.css"
import { Link } from "react-router-dom"
import {Section2data ,Section2data1,Section2data2} from "../Components/Data1"



export function Section2() {
    const section2data = Section2data.map((section2data) => {

        return(
            <div className="col-md-6">
                <div className="section2unun2">
                    <h5 className='section2h1'>{section2data.text1}</h5>
                    <h4 className='section2h2'>{section2data.text2}</h4>
                    <p className='section2p1'>{section2data.text3}</p> 
                    <div className="secbtnunn">
                    <a className="tf-button btn-effect sectn3" href="/"><span className="boder-fade"></span><span className="effect">{section2data.btntext1}</span></a>
                    </div>
                </div>
            </div>
        )
    })
    const section2data1 = Section2data1.map((section2data1) => {
        return(
        
          
                <div className="section2unun4 active">
           <h5 className='section2h3'>{section2data1.text1}</h5>
           <p className='section2p2'>{section2data1.text2}</p>
                </div>
                       
        )
    })
    const section2data2 = Section2data2.map((section2data2) => {
        return(
             <div className="section2unun5">
               <h5 className='section2h3'>{section2data2.text1}</h5>
           <p className='section2p2'>{section2data2.text2}</p>
             </div>
            )
        
    })
    return( 
        <>
      <div className="section2">
        <div className="section2unn1">
            <div className="container">
                <div className="row">
         {section2data}
         <div className="col-md-6 my-md-0 my-5">
         <div className="section2unun3">
         {section2data1}
         {section2data2}
         </div>
         </div>
                
                </div>
            </div>
        </div>
      </div>
   

        </>
    )
}


