import React, { Component } from 'react';

import '../App.css';
import '/node_modules/bootstrap/dist/css/bootstrap.min.css'
import 'bootstrap/dist/js/bootstrap.min'
import "/node_modules/font-awesome/css/font-awesome.min.css"
import { Link } from "react-router-dom";

import { Blogdata } from './Data1';
import { Multipalsld } from './slick-sld/Multipalsld';
import { Bigsld } from './slick-sld/Bigsld';




export function Blog() {
    const blogdata = Blogdata.map((blogdata) => {
    return(
        <>
         <h4 className='blogh1'>{blogdata.text}</h4>
         <h1 className='blogh2'>{blogdata.text1}</h1>
        </>
        
    )
    })

 
  
    return(
        <>
           <div className="blog">
            <div className="blogun1">
            <div className="container">
            <div className="row">
                <div className="col-md-12">
                <div className="blogunun1">
                {blogdata}
               
                </div>
                  
                </div>
            </div>
         </div> 
         <Bigsld />
         <div className="container">
            <div className="row">
                <div className="col-md-12">
                    <div className="blogunun2">
                    <a class="tf-button btn-effect blogbtn" href="/"><span class="boder-fade"></span><span class="effect">Explore more</span></a>
                    </div>
                </div>
            </div>
        </div>
            </div>
           </div>
        </>
    )

}
