import { red } from '@mui/material/colors';
import React, {useState, useEffect} from 'react'
import "../App.css"
import { Link } from "react-router-dom"
const Navbar3 = () => {
  const [show, setShow] = useState(true);
  const [lastScrollY, setLastScrollY] = useState(10);

  const controlNavbar = () => {
    if (typeof window !== 'undefined') { 
      if (window.scrollY > lastScrollY) { // if scroll down hide the navbar
        setShow(false); 
      } else { // if scroll up show the navbar
        setShow(true);  
      }

      // remember current page location to use in the next move
      setLastScrollY(window.scrollY); 
    }
  };

  useEffect(() => {
    if (typeof window !== 'undefined') {
      window.addEventListener('scroll', controlNavbar);

      // cleanup function
      return () => {
        window.removeEventListener('scroll', controlNavbar);
      };
    }
  }, [lastScrollY]);


return (
    
    <>

<>
<nav  className= {`active1 ${show && 'hidden1'}`}  style={{backgroundColor: "#141a31", }}>
<div className="container">
  <div className="navim">
    <Link to=""><img src="https://i.ibb.co/TtC9jSG/download.png" alt="" /></Link>
  </div>
  <ul>
    <li  className="menu-item">  <Link to="/"> Home</Link>
   
    </li>
    <li className='menu-item'> <Link to="/about"> About</Link>
    <ul className="sub-menu">
    <li>
    <Link aria-current="page" className="active" to="/about">About</Link>
    </li>
    <li><Link className="" to="/comming-soon">Coming Soon</Link></li>
    <li><Link className="" to="/community">Community</Link></li>
    <li><Link className="" to="/team-details">Team Details</Link></li>
    <li><Link className="" to="/faq">FaQ</Link></li>
    <li><Link className="" to="/404">404</Link></li>
    </ul></li>
    <li className='menu-item'> <Link to="/roadmap"> Road Map </Link></li>
    <li className='menu-item'> <Link to='/nft'> Nft </Link>
   
    </li>
    <li className='menu-item'> <Link to=""> Blog</Link>
    
     </li>
    <li className='menu-item'> <Link to="/contact"> Contact</Link></li>
  </ul>
  </div>
  <Link className="tf-button btn-effect" to="/contact"><span className="boder-fade"></span><span className="effect">join discord</span></Link>
</nav>
</>

    </>
)
}

export default Navbar3
