import React, { Component, useState } from 'react';
import '/node_modules/bootstrap/dist/css/bootstrap.min.css'
import 'bootstrap/dist/js/bootstrap.min'
import { Nftdata } from '../Data';
import Dropdown from 'react-bootstrap/Dropdown';
import {Autoslunnft} from "../Data"
import '../About/About.css'



export function Nftsec2() {
   const[visible ,setVisible] =useState(8);
   const showMoreiteams =() =>{
  setVisible(preValue => preValue + 4);
   }

    const autoslunnft = Autoslunnft.slice(0, visible).map((autoslunnft) => {
      return(
<>
  <div className="col-md-3 my-3">
          <div className="nftautosl1">
            <div className="img-box">
              <img src={autoslunnft.image} alt="" />
              <div className="content1">
             <a href="" className="section3h1 ">{autoslunnft.text}</a>
              </div>
            </div>
          </div>
          </div>
      
    </>
  
)}
    )
    const nftdata = Nftdata.map((nftdata) => {
        return(
            <>
             <div className="container">
        <div className="row">
            <div className="col-md-12">
                <h1 className='aboutsec1h1 '>{nftdata.text}</h1>
                </div>
                </div>
                </div>
            </>
        )
     })


    
    return( 
        <>
        <div className="roadmapsec1">
            <div className="roadmapsec1un1">
                {nftdata}
            </div>
        </div>
           <div className="nftsec">
            <div className="nftsecun1">
                <div className="container">
                    <div className="row">
                          <div className="col-md-4">
                            <div className="nftsecunun1">
                                <input type="text"placeholder='search' className='nftununinput1' />
                                <button className='nftsec1btn1'><i class="fa fa-search"></i></button>
                            </div>
                          </div>
                          <div className="col-md-8">
                            <div className="nftsecunun2">
                            <Dropdown>
      <Dropdown.Toggle variant="success" id="dropdown-basic">
      Default sorting
      </Dropdown.Toggle>

      <Dropdown.Menu>
        <Dropdown.Item href="#/action-1">Action</Dropdown.Item>
        <Dropdown.Item href="#/action-2">Another action</Dropdown.Item>
        <Dropdown.Item href="#/action-3">Something else</Dropdown.Item>
      </Dropdown.Menu>
    </Dropdown>
    <Dropdown className='mx-3'>
      <Dropdown.Toggle variant="success" id="dropdown-basic">
      All Categories
      </Dropdown.Toggle>

      <Dropdown.Menu>
        <Dropdown.Item href="#/action-1">Forgotten Samurai</Dropdown.Item>
        <Dropdown.Item href="#/action-2">Star Batter</Dropdown.Item>
        <Dropdown.Item href="#/action-3">Calvary Deneral</Dropdown.Item>
      </Dropdown.Menu>
    </Dropdown>
                            </div>  </div>
                          {autoslunnft}
                          <div className="col-md-12 mx-auto">
                          <div className="nftshowmmbtn1">
                          <a class="tf-button btn-effect sectn2 nftsectn2"  onClick={showMoreiteams}><span class="boder-fade"></span><span class="effect">Load More</span></a>
                          </div>
                          
                          </div>
                          
            
                    </div>
                </div>
            </div>
           </div>
         
        </>
    )
}


