import React, { Component } from 'react';
import { Swiper, SwiperSlide } from 'swiper/react';
import '../App.css';
import mainlgo from '../images/logo1.png'
import '/node_modules/bootstrap/dist/css/bootstrap.min.css'
import 'bootstrap/dist/js/bootstrap.min'
import "/node_modules/font-awesome/css/font-awesome.min.css"
import { Link } from "react-router-dom"
import {  Sliderun,Sliderunc1 } from './Data';



export function Section1() {
    const sliderun = Sliderun.map((sliderun)   => {
      return(
        <img src={sliderun.image} className="d-block w-100" alt="..." />
      )
    })
    const sliderun1 = Sliderun.map((sliderun1)   => {
      return(
        <img src={sliderun1.image2} className="d-block w-100" alt="..." />
      )
    })
    const sliderun2 = Sliderun.map((sliderun2)   => {
      return(
        <img src={sliderun2.image1} className="d-block w-100" alt="..." />
      )
    })
    const sliderunc1 = Sliderunc1.map((sliderunc1)   => {
      return(
        <>
       <div className="col-md-6">
           <div className="section1unun1">
            <h1>{sliderunc1.h1}</h1>
            <p>{sliderunc1.p1}</p>
            <a className="tf-button btn-effect sectn1" href="/"><span className="boder-fade"></span><span className="effect">Connect Wallet </span></a>
            <a className="tf-button btn-effect sectn2" href="/"><span className="boder-fade"></span><span className="effect">Watch Now</span></a>
           </div>

       </div>
      
      </>
      )
    })
    const sliderunc2 = Sliderunc1.map((sliderunc2)   => {
      return(
        <>
       <div className="col-md-6">
         <div className="section1unun2">
          <img src={sliderunc2.image} alt="" />
         </div>
        </div>
      </>
      )
    })

    const sliderunc3 = Sliderunc1.map((sliderunc3)   => {
      return(
        <>
       <div className="col-md-12">
           <div className="section1unun1">
            <h1 className='section1h3'>{sliderunc3.h1}</h1>
            <p className='section1p3'>{sliderunc3.p1}</p>
            <div className="btnunun">
            <a className="tf-button btn-effect sectn1" href="/"><span className="boder-fade"></span><span className="effect">Connect Wallet </span></a>
            <a className="tf-button btn-effect sectn2" href="/"><span className="boder-fade"></span><span className="effect nn1w">Watch Now</span></a></div>
           </div>

       </div>
      
      </>
      )
    })
  return (
    
    <>
    <div className="section1">
    <div className="overlay"></div>
        <div className="section1un1">
        
           
            <div id="carouselExample" className="carousel slide">
  <div className="carousel-inner">
    <div className="carousel-item active">
      {sliderun}
      <div className="section1up ">
       <div className="container">
        <div className="row">
         {sliderunc1}
         {sliderunc2}
        </div>
       </div>
      </div>
    </div>
    <div className="carousel-item">
   {sliderun1}
   <div className="section1up ">
       <div className="container">
        <div className="row">
         {sliderunc1}
         
        </div>
       </div>
      </div>
    </div>
    <div className="carousel-item">
     {sliderun2}
     <div className="section1up ">
       <div className="container">
        <div className="row">
         {sliderunc3}
         
        </div>
       </div>
      </div>
    </div>
  </div>
  <button className="carousel-control-prev" type="button" data-bs-target="#carouselExample" data-bs-slide="prev">
    <span className="carousel-control-prev-icon" aria-hidden="true"></span>
    <span className="visually-hidden">Previous</span>
  </button>
  <button className="carousel-control-next" type="button" data-bs-target="#carouselExample" data-bs-slide="next">
    <span className="carousel-control-next-icon" aria-hidden="true"></span>
    <span className="visually-hidden">Next</span>
  </button>
</div>
             </div>
            </div>
     
    </>
  );
}

