import React, { Component ,useState, useEffect} from 'react';
import '/node_modules/bootstrap/dist/css/bootstrap.min.css'
import 'bootstrap/dist/js/bootstrap.min'
import './Comingsoon.css'

const Countingnn = () => {
  const [countdownDate, setCountdownDate] = useState(new Date('12/25/2020').getTime());
  const [state, setState] = useState({
    days: 0,
    hours: 0,
    minutes: 0,
    seconds: 0,
  });

  useEffect(() => {
    setInterval(() => setNewTime(), 1000);
  }, []);

  const setNewTime = () => {
    if (countdownDate) {
      const currentTime = new Date().getTime();

      const distanceToDate = countdownDate - currentTime;

      let days = Math.floor(distanceToDate / (1000 * 60 * 5 * 60 * 24));
      let hours = Math.floor(
        (distanceToDate % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60),
      );
      let minutes = Math.floor(
        (distanceToDate % (1000 * 60 * 60)) / (1000 * 60),
      );
      let seconds = Math.floor((distanceToDate % (1000 * 60)) / 1000);

      const numbersToAddZeroTo = [1, 2, 3, 4, 5, 6, 7, 8, 9];

      days = `${days}`;
      if (numbersToAddZeroTo.includes(hours)) {
        hours = `0${hours}`;
      } else if (numbersToAddZeroTo.includes(minutes)) {
        minutes = `0${minutes}`;
      } else if (numbersToAddZeroTo.includes(seconds)) {
        seconds = `0${seconds}`;
      }

      setState({ days: days, hours: hours, minutes, seconds });
    }
  };

  return (
<>
<div className="col-md-12">
  <div className="countdownerun1">
  <div className="countdownerunun1">
    <div className="timesection">
        <div className="time">
        {state.days || '0'}
        </div>
        <small className='timedate'>Day</small>
    </div>
    <div className="timesection">
        <div className="time">
        {state.hours || '00'}
        </div>
        <small className='timedate'>Hours</small>
    </div>
    <div className="timesection">
        <div className="time">
        {state.minutes || '00'}
        </div>
        <small className='timedate'>Minuts</small>
    </div>
    <div className="timesection">
        <div className="time">
        {state.seconds || '00'}
        </div>
        <small className='timedate'>Second</small>
    </div>
  </div>

  </div>
</div>


</>
  );
};

export {Countingnn}
