import React, { Component } from 'react';

import './App.css';
import '../node_modules/bootstrap/dist/css/bootstrap.min.css'
import '../node_modules/bootstrap/dist/js/bootstrap.min.js'
import Home from './Components/Home';
import { BrowserRouter as Router, Routes, Route, Link, Switch,browserHistory  } from 'react-router-dom';
import { About } from './Components/About/About';
import { Comingsoon } from './Components/Comingsoon/Comingsoon';
import { Community } from './Components/community/Community';
import { Team } from './Components/Team-Details/Team';
import { Faqm } from './Components/Faq/Faqm';
import { Error1 } from './Components/404/404';
import { Roadmapm } from './Components/Roadmap/Roadmap';
import { Nft } from './Components/Nft/Nft';
import { Blogm } from './Components/Blog/Blogm';
import { Contactm } from './Components/Contact/Contactm';




function App() {
  return (
    <>   
    
             <Router>
             <Switch>

                <Route exact path="/" component={Home}/>
                <Route exact path="/about" component={About}/>
                <Route exact path="/comming-soon" component={Comingsoon}/>
                <Route exact path="/community" component={Community}/>
                <Route exact path="/team-details" component={Team}/>
                <Route exact path="/faq" component={Faqm}/>
                <Route exact path="/404" component={Error1}/>
                <Route exact path="/roadmap" component={Roadmapm}/>
                <Route exact path="/nft" component={Nft}/>
                <Route exact path="/blog" component={Blogm}/>
                <Route exact path="/contact" component={Contactm}/>
            
              
                </Switch>
             </Router>  
   </>

  );
};

export default App;
