import React, { Component } from "react";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css"; 
import "slick-carousel/slick/slick-theme.css";
import '../About/About.css'
import {Autoslun,Autoslun1} from "../Data1"
export function Autosliderbig(){
   
    const settings9 = {
        dots: true,
      infinite: true,
      slidesToShow: 4,
      slidesToScroll: 1,
      autoplay: true,
      speed: 1000,
      autoplaySpeed: 1000,
      cssEase: "linear",
      responsive: [
        {
          breakpoint: 1200,
          settings: {
            slidesToShow: 4,
            slidesToScroll: 3,
            infinite: true,
            dots: true
          }
        },
        {
          breakpoint: 1024,
          settings: {
            slidesToShow: 3,
            slidesToScroll: 3,
            infinite: true,
            dots: true
          }
        },
        {
          breakpoint: 600,
          settings: {
            slidesToShow: 2,
            slidesToScroll: 2,
            initialSlide: 2
          }
        },
        {
          breakpoint: 480,
          settings: {
            slidesToShow: 1,
            slidesToScroll: 1
          }
        }
      ]
    };
    const settings10 = {
      dots: false,
    infinite: true,
    slidesToShow: 4,
    slidesToScroll: 2,
    autoplay: true,
    speed: 1000,
    autoplaySpeed: 800,
    cssEase: "ease-out",
    responsive: [
      {
        breakpoint: 1200,
        settings: {
          slidesToShow: 4,
          slidesToScroll: 3,
          infinite: true,
          dots: true
        }
      },
      {
        breakpoint: 1024,
        settings: {
          slidesToShow: 3,
          slidesToScroll: 3,
          infinite: true,
          dots: true
        }
      },
      {
        breakpoint: 600,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 2,
          initialSlide: 2
        }
      },
      {
        breakpoint: 480,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1
        }
      }
    ]
  };
    const autoslun = Autoslun.map((autoslun) => {
      return(
    <>
  
          <div className="autosl1mmm">
            <div className="img-box">
              <img src={autoslun.image} alt="" />
              <div className="content1">
             <a href="" className="section3h1 ">{autoslun.text}</a>
              </div>
            </div>
          </div>
      
    </>
  
)}
    )
    const autoslun1 = Autoslun1.map((autoslun1) => {
      return(
    <>
  
          <div className="autosl1mmm">
            <div className="img-box">
              <img src={autoslun1.image} alt="" />
              <div className="content1">
             <a href="" className="section3h1 ">{autoslun1.text}</a>
              </div>
            </div>
          </div>
      
    </>
  
)}
    )
    return( 
        <>
         <div>
       
        <Slider className="slider1" {...settings9}> {autoslun}</Slider>
        <Slider {...settings10}> {autoslun1}</Slider>
       </div>
        </>
    )
}
