import React, { Component ,useState, useEffect} from 'react';
import '/node_modules/bootstrap/dist/css/bootstrap.min.css'
import 'bootstrap/dist/js/bootstrap.min'

import {Errordata } from '../Data';
import{Accordion1} from '../Accoridn'
import '../Faq/Faq.css'


export function Error2() {
    const errordata = Errordata.map((errordata) =>{
        return(
            <>
             <div className="col-md-12">
                        <div className="errorunun1">
                        <h1 className='errorsecc1h1'>{errordata.text}</h1>
                        <h2 className='errorsecc1h2'>{errordata.h1}</h2>
                        <p className='errorsecc1p1' >{errordata.p}</p>
                        <a class="tf-button btn-effect sectn2 errorbtnback" href={errordata.link}><span class="boder-fade"></span><span class="effect">BACK TO HOME</span></a>
                        </div>
                    </div>
            </>
        )
    })
    
    return( 
        <>
       <div className="error">
        <div className="errorun1">
            <div className="container">
                <div className="row">
                   {errordata}
                </div>
            </div>
        </div>
       </div>
     
        </>
    )
}


