import React, { Component ,useState, useEffect} from 'react';
import '/node_modules/bootstrap/dist/css/bootstrap.min.css'
import 'bootstrap/dist/js/bootstrap.min'
import { Countingdata } from '../Data';
import Navbar2 from '../Navbar2';
import Navbar3 from '../Navbar3';
import Navbar4 from '../Navbar4';
import Navbar1 from '../Navbar1';
import { Footer } from '../Footer';
import '../About/About.css'
import {Teamdata1} from '../Data'



export function TeamSec2() {
    const teamdata1 = Teamdata1.map((teamdata1) => {
        return(
            <>
              <div className="col-md-5">
                <div className="teamsec2unun1">
                    <img src={teamdata1.image} alt="" />
                </div>
              </div>
              <div className="col-md-7 my-md-0 my-5">
                <div className="teamsec2unun2">
                    <p className='teamsec2h1'>{teamdata1.test}</p>
                    <h4 className='teamsech2'>{teamdata1.text1}</h4>
                    <div className="teamsec2unbox">
                        <h4 className='teamsec2h3'>{teamdata1.text2}</h4>
                        <p>{teamdata1.p1}</p>
                        <p>{teamdata1.p2}</p>
                    </div>
                    <div className="teamsec2uncontect">
                        <h4 className='teamsec2h4'>{teamdata1.text4}</h4>
                        <p>{teamdata1.address}</p>
                        <p>{teamdata1.email}</p>
                        <ul>
                            <li>
                                <a href="">
                                <i class="fa fa-twitter"></i>
                                </a></li>
                                <li>
                                <a href="">
                                <i class="fa fa-facebook"></i>
                                </a></li>
                                <li>
                                <a href="">
                                <i class="fa fa-send-o"></i>
                                </a></li>
                                <li>
                                <a href="">
                                <i class="fa fa-reddit-alien"></i>
                                </a>
                            </li>
                        </ul>
                    </div>
                </div>
              </div>
            </>
        )
     })

    
    return( 
        <>
     <div className="teamsec1">
        <div className="teamsec2un1">
            <div className="container">
                <div className="row">
                 {teamdata1}
                </div>
            </div>
        </div>
     </div>
        </>
    )
}


