import React, { Component } from 'react';

import '../App.css';
import '/node_modules/bootstrap/dist/css/bootstrap.min.css'
import 'bootstrap/dist/js/bootstrap.min'
import "/node_modules/font-awesome/css/font-awesome.min.css"
import { Link } from "react-router-dom";

import { Clintlgdata } from './Data1';
import { Multipalsld } from './slick-sld/Multipalsld';
import { Clintlogsld } from './slick-sld/Clintlogsld';




export function Clintlg() {
    const clintlgdata = Clintlgdata.map((clintlgdata) => {
        return(
            <>
             <h4 className='blogh1'>{clintlgdata.text}</h4>
             <h1 className='blogh2'>{clintlgdata.text1}</h1>
            </>
            
        )
        })
    
    return(
        <>
           <div className="section4">
            <div className="section4un1">
               <div className="container">
                <div className="row">
                    <div className="col-md-12">
                    <div className="clintlgun1">
                    {clintlgdata}
                    </div>
                      
                    </div>
                    <div className="col-md-12">
                    <div className="clintlgun2">
                    <Clintlogsld />
                    </div>
                    
                    </div>
                </div>
               </div>
              
            </div>
           </div>
        </>
    )

}
