import React, { Component ,useState, useEffect} from 'react';
import '/node_modules/bootstrap/dist/css/bootstrap.min.css'
import 'bootstrap/dist/js/bootstrap.min'
import { Countingdata } from '../Data';
import Navbar2 from '../Navbar2';
import Navbar3 from '../Navbar3';
import Navbar4 from '../Navbar4';
import Navbar1 from '../Navbar1';
import { Communatysec1 } from './Communatysec1';
import { Section2 } from '../Section2';
import { Communatysec2 } from './Communatysec2';
import { Footer } from '../Footer';
import '../About/About.css'



export function Community() {
     const countingdata = Countingdata.map((countingdata) => {
        return(
            <>
             <div className="countaing1unun1">
                <img src={countingdata.image} alt="" />
                <h2 className='counting1h1'>
                    <span>{countingdata.text}</span>
                    <span>{countingdata.text1}</span>
                    <span>{countingdata.text2}</span>
                </h2>
                <h1 className='counting1h2'>
                    <span>{countingdata.span}</span>
                    <span>{countingdata.span1}</span>
                </h1>
             </div>
            </>
        )
     })

    
    return( 
        <>
     <Navbar1 />
    <Navbar2 />
    <Navbar3 />
    <Navbar4 />
     <Communatysec1 />
     <Section2 />
     <Communatysec2 />
     <div className="footermmd">
     <Footer /></div>
        </>
    )
}


