import React, { Component } from 'react';
import '/node_modules/bootstrap/dist/css/bootstrap.min.css'
import 'bootstrap/dist/js/bootstrap.min'
import { Aboutsecdata1,Aboutsecdata2 } from '../Data';
import './About.css'



export function Aboutsec1() {
     const aboutsecdata1 = Aboutsecdata1.map((aboutsecdata1) => {
        return(
            <>
             <div className="container">
        <div className="row">
            <div className="col-md-12">
                <h1 className='aboutsec1h1'>{aboutsecdata1.text}</h1>
                </div>
                </div>
                </div>
            </>
        )
     })

     const aboutsecdata2 = Aboutsecdata2.map((aboutsecdata2) => {
        return(
            <>
             <div className="container">
        <div className="row">
            <div className="col-md-12">
            <div className="abouts2unun2">
                <h1 className='aboutsec1h2'>{aboutsecdata2.text}</h1>
                <h4 className='aboutsec1h3'>{aboutsecdata2.text1}</h4>
                <p className='pd30 center'>{aboutsecdata2.text2}</p>
                </div>
                </div>
                </div>
                </div>
            </>
        )
     })
    return( 
        <>
     <div className="about">
    

            {aboutsecdata1}
            </div>
            <div className="abouts2">
                <div className="abouts2un1">
                 {aboutsecdata2}
                </div>
            </div>
     
        </>
    )
}


