import React, { Component } from "react";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css"; 
import "slick-carousel/slick/slick-theme.css";
import {Blogslddata} from "../Data1"
export function Bigsld(){
   
    const settings2 = {
        dots: false,
      infinite: false,
      slidesToShow: 3,
      slidesToScroll: 2,
      autoplay: false,
      speed: 1000,
      autoplaySpeed: 1000,
      cssEase: "linear",
      responsive: [
        {
          breakpoint: 1200,
          settings: {
            slidesToShow: 2,
            slidesToScroll: 3,
            infinite: true,
            dots: true
          }
        },
        {
          breakpoint: 1024,
          settings: {
            slidesToShow: 2,
            slidesToScroll: 3,
            infinite: true,
            dots: true
          }
        },
        {
          breakpoint: 850,
          settings: {
            slidesToShow: 2,
            slidesToScroll: 2,
            infinite: true,
            dots: true
          }
        },
        {
          breakpoint: 600,
          settings: {
            slidesToShow: 1,
            slidesToScroll: 1,
            initialSlide: 2
          }
        },
        {
          breakpoint: 480,
          settings: {
            slidesToShow: 1,
            slidesToScroll: 1
          }
        }
      ]
    };
   
    const blogslddata = Blogslddata.map((blogslddata) => {
      return(
    <>
  
     <div className="Blogsldd1">
        <div className="Blogsldd1un1">
            <div className="Blogslddunun1">
                <a href="" className="Blogsldd1a" >{blogslddata.text}</a>
                <div className="image">
                    <img src={blogslddata.image} alt="" />
                </div>
                <div className="content-item">
                    <div className="content">
                       <a href="" className="Blogsldd1a1">{blogslddata.text1}</a>
                       <h5 className="Blogsldd1h1">{blogslddata.text2}</h5>
                       <p className="Blogsldd1p1">{blogslddata.text3}</p>
                    </div>
                </div>
            </div>
        </div>
     </div>
      
    </>
  
)}
    )
    return( 
        <>
         <div>
       
        <Slider className="slider1" {...settings2}> {blogslddata}</Slider>
        
       </div>
        </>
    )
}
