import React, { Component,useState } from 'react';
import '/node_modules/bootstrap/dist/css/bootstrap.min.css'
import 'bootstrap/dist/js/bootstrap.min'
import { Communatydata,Communatydata1 } from '../Data';
import '../About/About.css'
import ReactDOM from 'react-dom'
import ModalVideo from 'react-modal-video'



export function Communatysec1() {
   
        const [isOpen, setOpen] = useState(false)
     const communatydata = Communatydata.map((communatydata) => {
        return(
            <>
             <div className="container">
        <div className="row">
            <div className="col-md-12">
                <h1 className='aboutsec1h1 '>{communatydata.text}</h1>
                </div>
                </div>
                </div>
            </>
        )
     })

     const communatydata1 = Communatydata1.map((communatydata1) => {
        return(
            <>
             <div className="container">
        <div className="row">
            <div className="col-md-12">
            <div className="abouts2unun2">
            
                <h4 className='aboutsec1h3 pd22 font56'>{communatydata1.text1}</h4>
              
                </div>
                </div>
                </div>
                </div>
            </>
        )
     })
    return( 
        <>
     <div className="about">
    

            {communatydata}
            </div>
            <div className="abouts2">
                <div className="abouts2un1">
                 {communatydata1}
                </div>  <ModalVideo channel='youtube' autoplay isOpen={isOpen} videoId="pqHHUk8Tag4" onClose={() => setOpen(false)} />
                <div className="communaytyunun1">
          <div className="container">
            <div className="row">
                <div className="col-md-12">
                    <div className="video">
                        <img src="https://cyboxreact.themesflat.com/static/media/video.d40ab6c9688aab1fd79a.jpg" alt="" />
                      
                        <div className="my-video">
                    
                        <a class="popup-youtube" ><svg   onClick={()=> setOpen(true)} width="27" height="35" viewBox="0 0 27 35" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M27 17.0001L0 0.500061V35.0001L27 17.0001Z" fill="#14C2A3"></path></svg></a>
                        </div>
                    </div>
                </div>
            </div>
          </div>
                </div>
            </div>
     
        </>
    )
}


