import React, { Component } from "react";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css"; 
import "slick-carousel/slick/slick-theme.css";
import {Clintlogoslddata,Clintlogoslddata1,Clintlogoslddata2} from "../Data1"
export function Clintlogsld(){
   
    const settings4 = {
        dots: false,
      infinite: false,
      slidesToShow: 6,
      slidesToScroll: 3,
      autoplay: false,
      speed: 2000,
      autoplaySpeed: 2000,
      cssEase: "linear",
      responsive: [
        {
          breakpoint: 1200,
          settings: {
            slidesToShow: 4,
            slidesToScroll: 3,
            infinite: true,
            dots: true
          }
        },
        {
          breakpoint: 1024,
          settings: {
            slidesToShow: 3,
            slidesToScroll: 3,
            infinite: true,
            dots: true
          }
        },
        {
          breakpoint: 600,
          settings: {
            slidesToShow: 2,
            slidesToScroll: 2,
            initialSlide: 2
          }
        },
        {
          breakpoint: 480,
          settings: {
            slidesToShow: 2,
            slidesToScroll: 2
          }
        }
      ]
    };
  
    // settings5settings5settings5settings5settings5

    const settings5 = {
        dots: false,
      infinite: false,
      slidesToShow: 6,
      slidesToScroll: 2,
      autoplay: false,
      speed: 2000,
      autoplaySpeed: 2000,
      cssEase: "linear",
      responsive: [
        {
          breakpoint: 1200,
          settings: {
            slidesToShow: 4,
            slidesToScroll: 3,
            infinite: true,
            dots: true
          }
        },
        {
          breakpoint: 1024,
          settings: {
            slidesToShow: 3,
            slidesToScroll: 3,
            infinite: true,
            dots: true
          }
        },
        {
          breakpoint: 600,
          settings: {
            slidesToShow: 2,
            slidesToScroll: 2,
            initialSlide: 2
          }
        },
        {
          breakpoint: 480,
          settings: {
            slidesToShow: 2,
            slidesToScroll: 2
          }
        }
      ]
    };
    const clintlogoslddata = Clintlogoslddata.map((clintlogoslddata) => {
      return(
    <>
  
       <div className="clintlogslun1">
        <div className="image">
            <img src={clintlogoslddata.image} alt="" />
        </div>
       </div>
      
    </>
  
)}
    )

    const clintlogoslddata1 = Clintlogoslddata1.map((clintlogoslddata1) => {
      return(
    <>
  
       <div className="clintlogslun1">
        <div className="image">
            <img src={clintlogoslddata1.image} alt="" />
        </div>
       </div>
      
    </>
  
)}
    )

    const clintlogoslddata2 = Clintlogoslddata2.map((clintlogoslddata2) => {
      return(
    <>
  
       <div className="clintlogslun1">
        <div className="image">
            <img src={clintlogoslddata2.image} alt="" />
        </div>
       </div>
      
    </>
  
)}
    )
    return( 
        <>
         <div>
       
        <Slider className="slider1" {...settings4}> {clintlogoslddata}</Slider>
        <Slider className="slider2" {...settings5}> {clintlogoslddata1}</Slider>
        <Slider className="slider2" {...settings5}> {clintlogoslddata2}</Slider>
       </div>
        </>
    )
}
